import {Link} from "react-router-dom";
import DefaultLayout from "./DefaultLayout";

import races_2024 from "./races.json";
import races_2023 from "./races_2023.json";
import races_2022 from "./races_2022.json";
import races_2021 from "./races_2021.json";

const toRow = function(race, i, year) {
    var cells = [];
    cells.push( <td key={`${year}_${race.name}_date`}>{race.date}</td> );

    if (race.link)
        cells.push( <td key={`${year}_${race.name}_name`}>
            <a href={race.link} target="_blank" rel="noreferrer">{race.name}</a>
            </td>
        );
    else
        cells.push( <td key={`${year}_${race.name}_name_nolink`}>{race.name}</td>);

    return (<tr key={`${year}_${race.name}_row`}>{cells}</tr>);
};


function Races(props) {
    const year = props.year;

    const race_json =
        (year === "2023") ? races_2023 :
        (year === "2022") ? races_2022 :
        (year === "2021") ? races_2021 :
        races_2024;
    const races = race_json.races;

    const rows = races.map((race, i) => toRow(race, i, year));

    return (
        <DefaultLayout>
            <h1 className="page-header">Races</h1>
            <table>
                <tbody>
                    <tr key={`${year}_header_row`}>
                        <th key={`${year}_date_header`}>Date</th>
                        <th key={`${year}_race_header`}>Race</th>
                    </tr>
                    {rows}
                </tbody>
            </table>

            <h2 className="sub-header">Races by Year</h2>
            <ul>
                <li><Link to="/races/2024">2024</Link></li>
                <li><Link to="/races/2023">2023</Link></li>
                <li><Link to="/races/2022">2022</Link></li>
                <li><Link to="/races/2021">2021</Link></li>
            </ul>

        </DefaultLayout>
    )

}

export default Races;